import React from 'react';

import styles from './Card.module.css';
import {IoCloseCircleOutline} from 'react-icons/io5';

export default function Card(props) {

  return (
      
      <section className={styles.card} >
        
        
        <div >
          {/* <p >Max</p> */}
          
           <span className={styles.item2}>
             <p className={styles.parrafo1}>{props.max}° / {props.min}°</p>
           </span>

          <button 
             onClick={props.onClose} className={styles.buttonx}> <IoCloseCircleOutline />
          </button>
          
          {/* <p >Min</p> */}
        </div>
        
        <div className={styles.item1}>
          <h4 className={styles.pprincipal}>{props.name}</h4>
        </div>

        <div className={styles.item3}>
        <img src={`http://openweathermap.org/img/wn/${props.img}@2x.png`}  alt='' />
        </div>

     </section>
    
  )


};