import React from 'react';
import Card from './Card';
import App from '../App';

/* Llevaba props en el parametro*/
export default function Cards( { cities, onClose }) {

  return (
    <>
 
 {cities.map( (city) => (
      <Card
      key = {city.id}
      name = {city.name} 
      max = {city.max}
      min = {city.min}
      img = {city.img}
      onClose = { () => onClose(city.id) }
      />
   ))}

   </>
  ) 
}