import React from 'react';

import styles from './Cprincipal.module.css';

export default function Card2(props) {

  return (
    
      <section className={styles.card} >
          {/* <button onClick={props.onClose}>X</button> */}
        
        <div>
        <span>
        <h1 className={styles.h1}>19:45</h1>
        <p className={styles.p}>Thrusday | Sep 21</p>
        </span>
        </div>

        <div className={styles.item2}>
          {/* <p >Max</p> */}
           <span>
             <p className={styles.parrafo1}>{props.max}°</p>
           </span>
          {/* <p >Min</p> */}
        </div>
        
        <div className={styles.item1}>
          <h4 className={styles.pprincipal}>{props.name}</h4>
        </div>

        <div className={styles.item3}>
        <img src={`http://openweathermap.org/img/wn/${props.img}@2x.png`} alt='Icono del Clima' />
        </div>

     </section>
    
  )


};