import React from 'react';
import './App.css';
import Card2 from './components/CPrincipal'
import Card from './components/Card.jsx';
import Cards from './components/Cards.jsx';
import Nav from './components/Nav.jsx';
import SearchBar from './components/SearchBar.jsx';
import fetchCity from './services/fetchCity';


import styles from './components/Card.module.css';

// const apiKey = process.env.REACT_APP_WEATHER_APIKEY;



function App() {
  // Nos devuelve un array (destructurin) Elemento pos 1: Estado, Elemento pos 2: setState para ese estado en especifico
  // Podemos tener muchos estados, no como en el componente tipo Clase que sólo nos permite un estado (cada componente puede tener su unico estado)
  const [data, setData] = React.useState([]) // Entre patentesís decimos cuál es el estado por defecto


  function onSearch(ciudad) {

    if (data.length > 2) { 
      alert("Limite excedido"); 
    }
    else {  
      fetchCity(ciudad, setData);
    }
  }


function handleOnClose(id) {
    setData(prevData => {
      return prevData.filter(city => city.id !== id)
    })
  }


  return (

    <div className="Appcontainer">
      <div className="App">

        {/* Barra del Buscador  */}
        <div className="container">
          <SearchBar
            onSearch={onSearch}
          />

        </div>
        {/* <hr /> */}

        {/* Cards de Clima */}
        <div className="main">
          <Cards className={styles.citys}
            cities={data} onClose={handleOnClose} />
          {/* data */}
        </div>

        {/* Card de Clima Principal */}
        <div className="cmain">

          {data.length > 0 &&
            <Card2
              max={data[data.length - 1].max} /* Cairns.main.temp_max*/
              min={data[data.length - 1].min} /*Cairns.main.temp_min */
              name={data[data.length - 1].name} /* Cairns.name*/
              img={data[data.length - 1].img} /*Cairns.weather[0].icon*/
            // onClose={() => alert(Cairns.name)}
            />}

        </div>

      </div>
    </div>


  );
}

export default App;
